<template>
    <div class="shopcar">
        <van-nav-bar 
        title="购物车"
        left-text="返回"
        left-arrow
        @click-left="back"
        >
            <template #right>
                <div @click="toggleEditStatus" v-if="shopbagData.length>0">{{isEdit?'完成':'编辑'}}</div>
            </template>
        </van-nav-bar>
        <div v-if="shopbagData.length > 0">
            <div class="shopbag-bg"></div>
            <div class="shopbag-box">
                <van-list
                    v-model="loading"
                    :finished="finished"
                    finished-text="哎呀，没有数据啦！"
                    offset="100"
                    @load="loadShopbagData"
                    >
                    <van-swipe-cell class="shopbag-cell" :disabled="isEdit" v-for="(item,index) in shopbagData" :key="item.sid" >
                        <div class="shopbag-item">
                            <div class="check">
                                <van-checkbox @change="simpleSelect" v-model="item.isChecked" icon-size="24px"></van-checkbox>
                            </div>
                            <div class="pro-info">
                                <div class="pro-img">
                                    <img class="auto-img" :src="item.small_img">
                                </div>
                                <div class="pro-text">
                                    <div class="text-box">
                                        <div class="pro-names">
                                            <div class="pro-name">{{item.name}}</div>
                                            <div class="pro-rule">{{item.rule}}</div>
                                        </div>
                                    <div class="pro-enname">{{item.enname}}</div>
                                    </div>
                                    <div class="count-box">
                                        <div class="pro-price">&yen;{{item.price}}</div>
                                        <div class="pro-count">
                                            <van-stepper  @change="updateShopbagCount(item.sid,item.count)" v-model="item.count"  theme="round" button-size="22" disable-input />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <template #right>
                            <van-button class="shopbag-remove-btn" square type="danger" text="删除" @click="removeShopbag([item.sid],index)" />
                        </template>
                    </van-swipe-cell>
                </van-list>
            </div>
            <van-submit-bar v-show="!isEdit" :price="total" button-text="提交订单" button-color="#0c34ba" @submit="buy">
                <van-checkbox v-model="checked" @click="allSelect">全选</van-checkbox>
            </van-submit-bar>
            <van-submit-bar v-show="isEdit" button-text="删除选择" button-color="#0c34ba" @submit="removeSelect">
                <van-checkbox v-model="checked" @click="allSelect">全选</van-checkbox>
            </van-submit-bar>
        </div>
        <div v-else>
            <van-empty description="购物袋没有数据，赶紧去逛一逛！" >
                <div class="guang" @click="goHome">逛一逛</div>
            </van-empty>
        </div>
    </div>
</template>

<script>
    import '../assets/less/shopcar.less'
    import {mapMutations,mapState} from "vuex"
    export default {
        name:'Shopcar',
        data() {
            return {
                //是否编辑商品
                isEdit:false,
                //是否全选
                checked:false,
                //购物袋数据
                shopbagData:[],
                //购物袋所有数据
                shopbagAllData:[],
                //是否加载  首次不触发
                loading:true,
                //是否加载完成
                finished:false,
                //开始截取的位置
                start:0,
                //截取的数据数
                count:5,
                //总金额
                total:0
            }
        },
        created(){
            this.getShopbagData();
        },
        methods:{
            ...mapMutations(["changeShopbagCount"]),
            //进入首页
            goHome(){
                this.$router.push({name:"Home"});
            },
            //返回上一级
            back(){
                if(this.shopbagData.length){
                    this.$router.go(-1);
                }else {
                    this.$router.push({name:"Home"});
                }
            },
            //切换编辑状态
            toggleEditStatus(){
                this.isEdit = !this.isEdit;
            },
            //获取购物车数据
            getShopbagData(){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/findAllShopcart',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == 5000){
                        if(res.data.result.length == 0) {
                            return ;
                        }
                        res.data.result.forEach(v => {
                            //添加一个属性
                            v.isChecked = false;
                        });
                        this.shopbagAllData = res.data.result;
                        this.shopbagData = this.shopbagAllData.slice(this.start,this.start+this.count);
                        this.start += this.count;
                        this.loading = false; 
                        //第一次加载数量不够，加载完成
                        if(this.shopbagAllData.length < this.count){
                            this.finished = true;
                        }
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //加载数据
            loadShopbagData() {
                let data = this.shopbagAllData.slice(this.start,this.start+this.count);
                this.shopbagData.push(...data);
                this.start += this.count;
                // this.loading = false;
                if(data.length > 0) {
                    this.checked = false;
                }
                //此次加载数量不够，加载完成
                if(data.length < this.count){
                    this.finished = true;
                    return ;
                }
            },
            //修改购物袋商品信息
            updateShopbagCount(sid,count) {
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/modifyShopcartCount',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        sid,
                        count
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == 6000){
                        this.sum();   
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //全选
            allSelect(){
                this.shopbagData.forEach( v => {
                    v.isChecked = this.checked;
                });
                this.sum();
            },
            //单选
            simpleSelect() {
                this.sum();
                //只要有一个没勾选，则全选不能勾选
                for(let i = 0;i < this.shopbagData.length;i++){
                    if(!this.shopbagData[i].isChecked) {
                        this.checked = false;
                        return;
                    }
                }
                this.checked = true;
            },
            //删除商品
            removeShopbag(sids,index){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString不存在，跳转到登录
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }            
                //加载提示
                this.$toast.loading({
                    message: '删除中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/deleteShopcart',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        sids:JSON.stringify(sids)
                    }
                }).then( res => {
                    //关闭加载提示
                    this.$toast.clear();
                    //tokenString不正确，跳转到登录
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:3000
                        });
                        //跳转到登录界面
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString正确
                    if(res.data.code == 7000){
                        //删除一个
                        if(index != undefined){
                            this.shopbagData.splice(index,1);
                        }else {
                            //删除多个
                            for(let i = 0;i < this.shopbagData.length;i++){
                                if(this.shopbagData[i].isChecked){
                                    this.shopbagData.splice(i,1);
                                    i--;
                                }
                            }
                        }
                        //修改购物袋数量
                        this.changeShopbagCount(this.shopbagCount - sids.length);
                        //计算金额
                        this.sum();
                        this.$nextTick(() => {
                            let cells = document.querySelectorAll(".shopbag-cell");
                            if(cells.length == 0 && this.shopbagData.length < this.shopbagAllData.length){
                                this.loadShopbagData();
                            }else {
                                let cellSize = cells[cells.length - 1].getBoundingClientRect();
                                let distance = cellSize.top;
                                let cellHeight = cellSize.height;
                                let innerHeight = window.innerHeight;
                                if(distance + cellHeight - innerHeight <= 50) {
                                    this.loadShopbagData();
                                }
                            }
                        })
                        this.$toast({
                            message: res.data.msg,
                            forbidClick: true,
                            duration:500
                        });
                        //判断是否是全选
                        for(let i = 0;i < this.shopbagData.length;i++){
                            if(!this.shopbagData[i].isChecked){
                                return ;
                            }
                        }
                        this.checked = true;
                    }
                }).catch(err => {
                    //请求失败
                    //关闭加载提示
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //删除选择的购物袋商品
            removeSelect(){
                //获取勾选的商品的sid
                let sids = [];
                //遍历
                this.shopbagData.forEach( v => {
                    if(v.isChecked){
                        sids.push(v.sid);
                    }
                })
                //没有勾选任何商品，做出提示
                if(sids.length == 0){
                    this.$toast({
                        message:'未选择商品',
                        forbidClick:true,
                        duration:3000
                    })
                    return;
                }
                this.removeShopbag(sids);
            },
            //计算勾选的商品总金额
            sum(){
                let total = 0;
                this.shopbagData.forEach(v => {
                    if(v.isChecked) {
                        total += v.price * v.count ;
                    }
                });
                this.total = total * 100;
            },
            //提交订单
            buy(){
                //商品数量为0,提示
                let sids = [];
                for(let j = 0;j < this.shopbagData.length;j++){
                    if(this.shopbagData[j].isChecked){
                        sids.push(this.shopbagData[j].sid);
                    }
                }
                if(sids.length == 0){
                    this.$toast({
                        message:"请选择商品",
                        duration:1000,
                        forbidClick:true
                    })
                    return;
                }
                //进入订单结算页面
                this.$router.push({name:'Pay',params:{sids}});
            }
        },
        computed:{
            ...mapState(['shopbagCount'])
        }
    }
</script>

